@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #f1c17d;
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
  height: 100%;
  width: 100%;
  /* padding-bottom: 60px; */
  display: flex;
  flex-direction: column;
}
.notes-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 15px;
  padding-bottom: 4em;
}

.t1-main {
  font-size: 20px;
  font-weight: bolder;
  padding-bottom: 1em;
  margin-bottom: 3em;
  border-bottom: 2px solid #6a4919;
  color: #6a4919;
  margin-top: 1em;
}
.note__logo {
  position: fixed;
  bottom: 50px;
  left: 35px;
  background-color: #f1c103;
  cursor: pointer;
  display: none;
  width: 210px;
  height: 80px;
  border-radius: 12px;
}
.note__logo img {
  position: relative;
  top: -50px;
  width: 210px;
  height: 206px;
}

.modal-content {
  background-color: #d7974c !important;
  border-radius: 1rem !important;
  border-color: #865439;
}
.modal-header {
  border-bottom: 1px solid #5c3d2e !important;
}
.modal-footer {
  border-top: 1px solid #5c3d2e !important;
}
.modal {
  top: 20% !important;
}
.btn-primary,
.btn-primary:hover {
  background-color: #b85c38 !important;
  border: transparent !important;
}

.btn-secondary,
.btn-secondary:hover {
  background-color: #000 !important;
  border: transparent !important;
}

.mb-3 {
  margin-top: 0.2em;
}

.App .add-button {
  position: fixed;
  bottom: 45px;
  right: 45px;
  border-radius: 50%;
  font-size: 32px;
  width: 50px;
  height: 50px;
  z-index: 3;
  padding: 0;
}
.App .add-button:focus {
  outline: none;
  box-shadow: none;
}
.App .add-button:active {
  box-shadow: 0 0 0 0.25rem rgb(66 70 73 / 50%);
  outline: 0;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App .add-button {
    bottom: 30px;
    right: 25px;
  }
  .t1-main {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 15px;
    border-bottom: none;
  }
  .note__logo {
    display: none;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (max-width: 768px) {
  .App .add-button {
    bottom: 30px;
    right: 25px;
  }
  .t1-main {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 18px;
    border-bottom: none;
  }
  .note__logo {
    display: none;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (max-width: 992px) {
  .note__logo {
    display: none;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
}

.note {
  background-color: #feff9c;
  text-decoration: none;
  color: #000;
  margin: 1em;
  transition: all ease 0.3s;
  height: 10em;
  width: 10em;
  display: flex;

  flex-direction: column;
  -webkit-transform: rotate(-6deg);
          transform: rotate(-6deg);
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px,
    rgba(0, 0, 0, 0.1) 0px 2px 4px 0px,
    rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}

.note:nth-child(even) {
  -webkit-transform: rotate(4deg);
          transform: rotate(4deg);
  position: relative;
  top: 5px;
  background-color: #7afcff;
}

.note:nth-child(3n) {
  -webkit-transform: rotate(-3deg);
          transform: rotate(-3deg);
  position: relative;
  top: -5px;
  background-color: #ff7eb9;
}
*:focus {
  outline: 0;
}
.note__text {
  flex: 1 1;
  padding-right: 0.5em;
  padding-left: 1em;
  margin-bottom: 0.5em;
  margin-right: 9px;
  overflow: auto;
}

.note__text::-webkit-scrollbar {
  width: 4px;
}
.note__text::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}
.note__text::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px grey;
}

.note:hover,
.note.note--focused {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  z-index: 5;
  border-radius: 1em;
}
.note__tools {
  text-align: left;
  position: absolute;
  left: 7px;
  top: 2px;
  font-size: 13px;
}
.note__tools a,
.note__tools a:hover {
  color: blue;
}

.note__tools a {
  opacity: 0.6;
}
.note__tools a:hover {
  opacity: 1;
}

.note__tools button {
  border: transparent;
  background-color: transparent;
  opacity: 0.6;
  color: blue;
}
.note__tools button:hover {
  opacity: 1;
}
