.note .btn-close {
  position: absolute;
  top: 5px;
  right: 12px;
  width: 3px;
  height: 9px;
}

.note__pin {
  color: #b05b3b;
  border: transparent;
  align-self: center;
  background-color: transparent;
}
.note__pin:active,
.note__pin.note__pinned {
  color: #6a492b;
}

